  product_name: string; // Ï†úÌíàÎ™Ö
  product_code: string; // Ï†úÌíàÏΩîÎìú
  spec: string; // Í∑úÍ≤©
  order_qty: number; // Ï£ºÎ¨∏ÏàòÎüâ
  stock_qty: number; // Ïû¨Í≥†ÏàòÎüâ
  note: string; // ÎπÑÍ≥†
